<template>
    <v-row id="admininfovue" align="center" justify="center">
        <v-col cols="12" sm="12" md="12" lg="12" class="py-0 white">
            <v-sheet
            color="white"
            class="pa-5"
            >
                <div>
                    <div class="dotted mr-2"></div>
                    <h2 class="d-inline-block">관리자 정보</h2>
                    <h3 class="d-inline-block grey--text pl-2"><small>학원 정보를 수정합니다.</small></h3>
                </div>

                <div>
                    <v-tabs
                    v-model="tab"
                    background-color="white"
                    color="success"
                    class="elevation-0"
                    dark
                    >
                        <v-tabs-slider></v-tabs-slider>

                        <v-tab :key="1" :href="`#tab-1`">
                            정보 수정
                        </v-tab>
                        <v-tab :key="2" :href="`#tab-2`">
                            로고 설정
                        </v-tab>
                        <v-tab :key="3" :href="`#tab-3`">
                            학습 설정
                        </v-tab>

                        <v-tab-item
                        v-for="i in tabs"
                        :key="i"
                        :value="'tab-' + i"
                        >

                            <div v-if="i == 1">
                                <v-row>
                                    <v-col cols="12" sm="7" md="6" lg="4" style="margin: 0 auto;">
                                        <v-form ref="form" v-model="valid" class="mt-3">

                                            <label for="product">상품</label>
                                            <v-text-field
                                            v-model="a_product"
                                            solo dense single-line filled flat outlined disabled color="success"
                                            >
                                            </v-text-field>

                                            <label for="product">이름</label>
                                            <v-text-field
                                            v-model="a_name"
                                            solo dense single-line flat outlined color="success"
                                            :disabled="!input_disabled"
                                            :class="[!input_disabled ? 'v-text-field--filled' : '']"
                                            >
                                            </v-text-field>

                                            <label for="product">휴대폰 번호</label>
                                            <v-text-field
                                            v-model="a_phone"
                                            solo dense single-line flat outlined color="success"
                                            :disabled="!input_disabled"
                                            :class="[!input_disabled ? 'v-text-field--filled' : '']"
                                            >
                                            </v-text-field>

                                            <label for="product">학원명</label>
                                            <v-text-field
                                            v-model="c_name"
                                            solo dense single-line flat outlined color="success"
                                            :disabled="!input_disabled"
                                            :class="[!input_disabled ? 'v-text-field--filled' : '']"
                                            >
                                            </v-text-field>

                                            <label for="product">아이디</label>
                                            <v-text-field
                                            v-model="a_loginid"
                                            solo dense single-line filled flat outlined disabled color="success"
                                            >
                                            </v-text-field>

                                            <label for="product">비밀번호</label>
                                            <v-text-field
                                            v-model="a_password"
                                            solo dense single-line flat outlined color="success"
                                            type="password"
                                            class="msfont"
                                            :disabled="!input_disabled"
                                            :class="[!input_disabled ? 'v-text-field--filled' : '']"
                                            >
                                            </v-text-field>

                                            <div class="text-center" style="width: 50%; margin: 0 auto;">
                                                <v-btn
                                                :disabled="!valid_oper"
                                                color="success" depressed
                                                @click="changeAcademy"
                                                style="width: 100%;"
                                                >
                                                    {{ admin_info_text }}
                                                </v-btn>
                                            </div>
                                        </v-form>
                                    </v-col>
                                </v-row>
                            </div>

                            <div v-if="i == 2">
                                <v-row>
                                    <v-col class="text-center mt-7">
                                        <v-btn fab small disabled elevation="0">
                                            <v-icon dark>mdi-camera</v-icon>
                                        </v-btn>
                                        <span class="group pa-2">
                                            를 눌러 학원 로고 이미지를 선택해주세요.
                                        </span>
                                    </v-col>
                                </v-row>

                                <v-row align="center" justify="center">
                                    <v-col cols="12" sm="8" md="7" lg="6" class="text-center">
                                        <div style="border: 2px solid lightgray; border-radius: 4px;">

                                            <v-img
                                            src="@/assets/logo1.png"
                                            aspect-ratio="1"
                                            class="my-8"
                                            contain height="80"
                                            >
                                            </v-img>

                                            <div class="text-right mb-2">
                                                <v-btn class="mx-2" fab small elevation="0" style="color: rgba(0, 0, 0, 0.26) !important; background: rgba(0, 0, 0, 0.12) !important;" @click="changeLogoConfirm">
                                                    <v-icon dark>mdi-camera</v-icon>
                                                </v-btn>
                                            </div>
                                        </div>
                                    </v-col>
                                </v-row>

                            </div>

                            <div v-if="i == 3">

                                <v-row align="center" justify="center"
                                v-for="i in 4" :key="`setting-row-${i}`">
                                    <v-col cols="12" sm="8" md="8" lg="6">
                                        <h3 v-if="i==1"><b>교재 순서</b><small class="grey--text ml-4">교재 컨텐츠 학습 순서를 선택하세요.</small></h3>
                                        <h3 v-if="i==2"><b>학습 단계</b><small class="grey--text ml-4">단계별 과정의 학습 여부를 선택하세요.</small></h3>
                                        <h3 v-if="i==3"><b>채점 여부</b><small class="grey--text ml-4">단계별 학습 결과 채점 여부를 선택하세요.</small></h3>
                                        <h3 v-if="i==4"><b>듣기 학습</b><small class="grey--text ml-4">리스닝 과정의 학습량을 제어할 수 있습니다.</small></h3>

                                        <v-row>
                                            <v-col class="pb-2">
                                                <v-btn v-if="i==1" id="is_random_0" rounded block elevation="0" :class="[is_random==0 ? 'selected-setting':'', 'grey--text']" @click="clickSetting('is_random', 0)">순서대로 학습</v-btn>
                                                <v-btn v-if="i==2" id="is_skip_0" rounded block elevation="0" :class="[is_skip==0 ? 'selected-setting':'', 'grey--text']" @click="clickSetting('is_skip', 0)">학습단계 스킵 불가</v-btn>
                                                <v-btn v-if="i==3" id="is_scoring_0" rounded block elevation="0" :class="[is_scoring==0 ? 'selected-setting':'', 'grey--text']" @click="clickSetting('is_scoring', 0)">실시간 채점</v-btn>
                                                <v-btn v-if="i==4" id="is_controllable_0" rounded block elevation="0" :class="[is_controllable==0 ? 'selected-setting':'', 'grey--text']" @click="clickSetting('is_controllable', 0)">학습자 제어 가능</v-btn>
                                            </v-col>
                                            <v-col class="pb-2">
                                                <v-btn v-if="i==1" id="is_random_1" rounded block elevation="0" :class="[is_random==1 ? 'selected-setting':'', 'grey--text']" @click="clickSetting('is_random', 1)">랜덤 순서대로 학습</v-btn>
                                                <v-btn v-if="i==2" id="is_skip_1" rounded block elevation="0" :class="[is_skip==1 ? 'selected-setting':'', 'grey--text']" @click="clickSetting('is_skip', 1)">학습단계 스킵 가능</v-btn>
                                                <v-btn v-if="i==3" id="is_scoring_1" rounded block elevation="0" :class="[is_scoring==1 ? 'selected-setting':'', 'grey--text']" @click="clickSetting('is_scoring', 1)">학습 후 일괄 채점</v-btn>
                                                <v-btn v-if="i==4" id="is_controllable_1" rounded block elevation="0" :class="[is_controllable==1 ? 'selected-setting':'', 'grey--text']" @click="clickSetting('is_controllable', 1)">관리자 직접 설정</v-btn>
                                            </v-col>
                                        </v-row>
                                    </v-col>
                                </v-row>


                                <v-row align="center" justify="center">
                                    <v-col cols="12" sm="8" md="8" lg="6" class="text-center">
                                        <v-btn
                                        dense
                                        color="success"
                                        :disabled="!setting_disabled"
                                        @click="openDialog('change_setting_confirm')"
                                        >
                                            수정
                                        </v-btn>
                                    </v-col>
                                </v-row>

                            </div>

                        </v-tab-item>
                    </v-tabs>
                </div>

            </v-sheet>
        </v-col>

        <Dialogs>
            <div slot="change_academy_success">
                <v-card-text class="text-center">
                    <h4><b>학원 정보가 수정되었습니다.</b></h4>
                </v-card-text>

                <v-card-actions class="pa-0" style="border-top: 1px solid lightgray;">
                    <v-btn x-large text style="border-radius: 0; border-bottom-left-radius: 4px; border-right: 1px solid lightgray; width: 100%;" @click="closeDialog">
                        <b class="success--text">확인</b>
                    </v-btn>
                </v-card-actions>
            </div>
            <div slot="change_img">
                <v-card-text class="text-center">
                    <v-row>
                        <v-col class="px-8 text-right">
                            <v-btn color="grey" fab x-large dark style="height: 72px !important;">
                                <v-icon>mdi-folder-multiple-image</v-icon>
                            </v-btn>
                            <br />
                            <div class="mr-5 mt-4"><b>갤러리</b></div>
                        </v-col>
                        <v-divider vertical></v-divider>
                        <v-col class="px-8 text-left">
                            <v-btn color="grey" fab x-large dark style="height: 72px !important;">
                                <v-icon>mdi-camera</v-icon>
                            </v-btn>
                            <br />
                            <div class="ml-5 mt-4"><b>카메라</b></div>
                        </v-col>
                    </v-row>
                </v-card-text>

                <!-- Two Button -->
                <v-card-actions class="pa-0" style="border-top: 1px solid lightgray;">
                    <v-btn x-large class="ml-0" text style="width:100%;" @click="closeDialog">
                        <b>기본 로고로 변경</b>
                    </v-btn>
                </v-card-actions>
            </div>

            <div slot="change_setting_confirm">
                <v-card-text class="text-center">
                    <h4><b>학습 설정을 수정하시겠습니까?</b></h4>
                </v-card-text>

                <!-- Two Button -->
                <v-card-actions class="pa-0" style="border-top: 1px solid lightgray;">
                    <v-btn x-large text style="border-radius: 0; border-bottom-left-radius: 4px; border-right: 1px solid lightgray; width: 50%" @click="submitSetting">
                        <b class="success--text">수정</b>
                    </v-btn>
                    <v-btn x-large class="ml-0" text style="width:50%;" @click="closeDialog">
                        <b>취소</b>
                    </v-btn>
                </v-card-actions>
            </div>

        </Dialogs>

    </v-row>
</template>

<style lang="scss">
@import '@/assets/scss/views/startpage/admininfo.scss';
</style>

<script>
import { mapGetters } from "vuex";
import LogoImg from "@/components/common/LogoImg"
import Dialogs from "@/components/common/Dialogs"
import {bus} from '@/main'

export default {
    name: "studentoper",
    components: {
        LogoImg, Dialogs
    },
    data () {
        return{
            tab: null, // null
            tabs: 3,

            valid : true,
            valid_oper : true,
            a_product : '',
            a_loginid : '',

            a_name : '',
            a_phone: '',
            c_name: '',
            a_password: '',

            old_name : '',
            old_phone: '',
            old_cname: '',
            old_password: '',

            admin_info_text : '확인',
            input_disabled : false,
            // change status
            cs1: 0,
            cs2: 0,
            cs3: 0,
            cs4: 0,

            setting_disabled : false,
            is_random: 0,
            is_skip: 0,
            is_scoring: 0,
            is_controllable: 0,

            old_random: 0,
            old_skip: 0,
            old_scoring: 0,
            old_controllable: 0,

            cas1:0, cas2:0, cas3:0, cas4:0,
        }
    },
    methods: {
        changeAcademy () {
            this.$axios.post(`${this.$host}/academy/` + this.$cid + '/changeacademy', {
                president : this.a_name,
                phone : this.a_phone,
                cname : this.c_name,
                password : this.a_password
            }).then((res) => {
                if(res.status == 200){
                    let json = this.$storage.get('user');
                    json.president = this.a_name;
                    json.phone = this.a_phone;
                    json.cname = this.c_name;
                    json.password = this.a_password;
                    this.$storage.set('user', json);
                    this.admin_info_text = '확인';
                    this.valid_oper = false;

                    this.old_name  = this.a_name;
                    this.old_phone = this.a_phone;
                    this.old_cname = this.c_name;
                    this.old_password = this.a_password;

                    this.openDialog("change_academy_success");
                }
            }).catch((err) => {  console.log(err);  });

        },
        changeStatus (idx, status){

            if( status == true ){
                this['cs' + idx] = 1;
                this.admin_info_text = '변경';
                this.valid_oper = true;
            }
            else{
                this['cs' + idx] = 0;
                if( this.cs1 == 0 && this.cs2 == 0 && this.cs3 == 0 && this.cs4 == 0 ){
                    this.admin_info_text = '확인';
                    this.valid_oper = false;
                }
            }

        },
        changeLogoConfirm () {
            this.openDialog('change_img');
        },
        clickSetting(sid, idx){
            let item = document.getElementById(sid + '_' + idx);
            if(idx == 0){
                document.getElementById(sid + '_' + 1).classList.remove("selected-setting");
                item.classList.add("selected-setting");
                this[sid] = 0;
            }
            else if(idx == 1){
                document.getElementById(sid + '_' + 0).classList.remove("selected-setting");
                item.classList.add("selected-setting");
                this[sid] = 1;
            }
        },
        changeSettingOption(idx, status){
            if( status == true ){
                this['cas' + idx] = 1;
                this.setting_disabled = true;
            }
            else{
                this['cas' + idx] = 0;
                if( this.cas1 == 0 && this.cas2 == 0 && this.cas3 == 0 && this.cas4 == 0 ){
                    this.setting_disabled = false;
                }
            }
        },
        submitSetting(){

            this.$axios.post(`${this.$host}/academy/` + this.$cid + '/modifyconfig', {
                is_random : this.is_random,
                is_skip: this.is_skip,
                is_scoring: this.is_scoring,
                is_controllable: this.is_controllable
            }).then((res) => {
                if(res.status == 200){
                    let json = this.$storage.get('user');
                    json.is_random = this.is_random;
                    json.is_skip = this.is_skip;
                    json.is_scoring = this.is_scoring;
                    json.is_controllable = this.is_controllable;
                    this.$storage.set('user', json);

                    this.old_random = this.is_random;
                    this.old_skip = this.is_skip;
                    this.old_scoring = this.is_scoring;
                    this.old_controllable = this.is_controllable;

                    this.closeDialog();
                    this.setting_disabled = false;
                }
            }).catch((err) => {
                console.log(err);
            });

        }


    },
    created : function () {
        let uclass = this.$storage.get('user').uclass;

        this.a_name = this.$storage.get('user').president;
        this.c_name = this.$storage.get('user').cname;
        this.a_phone = this.$storage.get('user').phone;
        this.a_password = this.$storage.get('user').password;

        this.old_name = this.$storage.get('user').president;
        this.old_phone = this.$storage.get('user').phone;
        this.old_cname = this.$storage.get('user').cname;
        this.old_password = this.$storage.get('user').password;

        if( this.$store.state.UCLASS_PRESIDENT == parseInt(uclass) ){
            this.input_disabled = true;
        }
    },
    mounted () {
        this.is_random = this.$storage.get('user').is_random;
        this.is_skip = this.$storage.get('user').is_skip;
        this.is_scoring = this.$storage.get('user').is_scoring;
        this.is_controllable = this.$storage.get('user').is_controllable;

        this.old_random = this.$storage.get('user').is_random;
        this.old_skip = this.$storage.get('user').is_skip;
        this.old_scoring = this.$storage.get('user').is_scoring;
        this.old_controllable = this.$storage.get('user').is_controllable;


        // document.getElementById('is_random_' + this.$storage.get('user').is_random).classList.add("selected-setting");
        // document.getElementById('is_skip_' + this.$storage.get('user').is_skip).classList.add("selected-setting");
        // document.getElementById('is_scoring_' + this.$storage.get('user').is_scoring).classList.add("selected-setting");
        // document.getElementById('is_controllable_' + this.$storage.get('user').is_controllable).classList.add("selected-setting");
    },
    watch: {
        a_name (val) {
            if( this.old_name != val && val != "" )
                this.changeStatus(1, true);
            else
                this.changeStatus(1, false);
        },
        a_phone (val) {
            if( this.old_phone != val && val != "" )
                this.changeStatus(2, true);
            else
                this.changeStatus(2, false);
        },
        c_name (val){
            if( this.old_cname != val && val != "" )
                this.changeStatus(3, true);
            else
                this.changeStatus(3, false);
        },
        a_password (val) {
            if( this.old_password != val && val != "" )
                this.changeStatus(4, true);
            else
                this.changeStatus(4, false);
        },
        is_random(val) {
            if( this.old_random != val ) this.changeSettingOption(1, true);
            else this.changeSettingOption(1, false);
        },
        is_skip(val) {
            if( this.old_skip != val ) this.changeSettingOption(2, true);
            else this.changeSettingOption(2, false);
        },
        is_scoring(val) {
            if( this.old_scoring != val ) this.changeSettingOption(3, true);
            else this.changeSettingOption(3, false);
        },
        is_controllable(val) {
            if( this.old_controllable != val ) this.changeSettingOption(4, true);
            else this.changeSettingOption(4, false);
        },
    },
    computed : {
        ...mapGetters([
            'getUclass'
        ])
    }

};
</script>
