<template>
<v-row id="lessonschedulevue" justify="center" align="stretch" class="height-inherit">
    <v-col cols="12" xs="12" sm="3" md="3" lg="3" class="pa-0 pr-1 height-inherit">
        <div
        :class="['bg-white height-inherit', class_item_select_status == true ? 'mb-1': '']"
        :style="class_item_select_status == true ? 'height: 92.5% !important;' : ''"
        >
            <v-subheader class="justify-center title grey lighten-4 ">
                <v-select outlined solo hide-details dense flat label="Class"
                :items="class_items" @change="getStudentInClass">

                    <!-- <template v-slot:selection="{ item, index }">
                        <v-checkbox color="info"
                        @click="selectClassAll($event, item.value)"
                        >
                        </v-checkbox>
                        <span>{{ item.text }}</span>
                    </template> -->

                </v-select>
            </v-subheader>

            <v-row v-if="class_item_select_status == false">
                <v-col class="text-center">
                    <span class="grey--text">클래스를 선택하세요.</span>
                </v-col>
            </v-row>
            <v-row v-else>
                <v-col cols="12" class="py-0" >

                    <v-list class="py-0">
                        <v-list-item-group
                        v-model="class_students_settings"
                        multiple
                        >
                            <v-list-item
                            v-for="(item, i) in class_students" :key="`class_students_key_${i}`"
                            active-class="info--text"
                            @click="changeStudentCheckbox(item.id)"
                            style="border-bottom: 1px solid lightgray;"
                            >
                                <template v-slot:default="{ active }">
                                    <v-list-item-action>
                                        <v-checkbox v-model="active" color="info"
                                        >
                                        </v-checkbox>
                                    </v-list-item-action>

                                    <v-list-item-content>
                                        <v-list-item-title>{{ item.text }}</v-list-item-title>
                                    </v-list-item-content>
                                </template>
                            </v-list-item>
                        </v-list-item-group>
                    </v-list>
                </v-col>
            </v-row>
        </div>

        <v-row class="bg-white text-center"
        v-if="class_item_select_status == true"
        style="height: calc(7.5% - 4px); width: 100%; margin: 0 auto;"
        >
            <v-col class="py-0" :align-self="'center'">
                <b class="mr-2">수업 요일</b>
                <span>{{ class_item_lesson_day }}</span>
            </v-col>
        </v-row>

    </v-col>

    <v-col cols="12" xs="12" sm="6" md="6" lg="6" class="pa-0 height-inherit">
        <div
        :class="['bg-white height-inherit', class_item_select_status == true ? 'mb-1': '']"
        :style="class_item_select_status == true ? 'height: 92.5% !important;' : ''"
        >
            <v-subheader class="justify-center title grey lighten-4">
                <b style="font-size: 1rem !important;">{{ (select_class_students.length == 0 ? '' : '교재 목록') }}</b>
            </v-subheader>

            <v-tabs
            v-if="select_class_students.length > 0"
            v-model="tab" background-color="white"
            color="success" class="elevation-0" grow
            >
                <v-tabs-slider></v-tabs-slider>

                <v-tab
                v-for="i in tabs"
                :key="`tabkey-${i}`" :href="`#tab-${i}`"
                class="border-b-1-grey" @click="changeTab"
                >
                    <span v-if="i == 1">교과서</span>
                    <span v-if="i == 2">수능/모의고사</span>
                    <span v-if="i == 3">만든 교재</span>
                </v-tab>

                <v-tab-item
                v-for="i in tabs"
                :key="`tabitemkey-${i}`"
                :value="'tab-' + i"
                >
                    <div v-if="i != 3">
                        <v-row style="width: 100%; margin: 0 auto;">
                            <v-col class="px-0 py-1 border-b-1-grey">
                                <v-btn
                                v-for="(item, x) in grade_items"
                                :key="`gradeitemtabkey-${x}`"
                                :id="`gradeitembtn-${i}-${item.value}`"
                                depressed small outlined
                                active-class="success"
                                :class="[`pa-0 schedule_grade_items_${i}`, x == 0 ? 'success white--text' : '']"
                                style="color: black; border: 1px solid #9e9e9e;"
                                @click="selectGradeBtnEvent(i, item.value)"
                                >
                                    {{ item.text }}
                                </v-btn>
                            </v-col>
                        </v-row>
                    </div>

                    <v-row>
                        <v-col cols="12" class="py-0"
                        >
                            <v-list class="py-0">
                                <v-list-item-group
                                v-model="select_books_settings[i]"
                                multiple
                                active-class="info--text"
                                >

                                    <v-list-item
                                    v-for="(item, y) in book_items[i+1]"
                                    :key="`bookitemkey-${i}-${y}`"
                                    @change="selectBook(item.id)"
                                    :class="[select_grade[i] != 'all' ? (select_grade[i] == item.grade ? '' : 'd-none') : '']"
                                    style="border-bottom: 1px solid lightgray;"
                                    >
                                        <template v-slot:default="{ active }">
                                            <v-list-item-action>
                                                <v-checkbox v-model="active" color="info">
                                                </v-checkbox>
                                            </v-list-item-action>

                                            <v-list-item-content>
                                                <v-list-item-title>{{ item.bookname }}</v-list-item-title>
                                            </v-list-item-content>
                                        </template>
                                    </v-list-item>
                                </v-list-item-group>
                            </v-list>
                        </v-col>
                    </v-row>

                </v-tab-item>
            </v-tabs>
        </div>
        <v-row class="bg-white text-center"
        v-if="class_item_select_status == true"
        style="height: calc(7.5% - 4px); width: 100%; margin: 0 auto;"
        >
            <v-col class="py-0 px-1" :align-self="'center'"
            v-if="select_book_items.length > 0"
            >
                <b class="mr-2">교재 정보</b>
                <small>출판사 YBM</small>
                <small class="grey--text"> | </small>
                <small>출판연도 2015 개정교육과정</small>
                <small class="grey--text"> | </small>
                <small>학습일차 레슨 8</small>
            </v-col>
        </v-row>
    </v-col>

    <v-col cols="12" xs="12" sm="3" md="3" lg="3" class="pa-0 pl-1 height-inherit">
        <div
        :class="['bg-white height-inherit', class_item_select_status == true ? 'mb-1': '']"
        :style="class_item_select_status == true ? 'height: 92.5% !important;' : ''"
        >

            <v-subheader class="justify-center title grey lighten-4">
                <b style="font-size: 1rem !important;">{{ select_book_items.length > 0 ? '학습 상세설정' : '' }}</b>
            </v-subheader>

            <section v-if="select_book_items.length > 0">
            <v-row style="width: 100%; margin: 0 auto;">
                <v-col cols="12" class="pa-2">
                    <b class="success--text">학습 과정</b>
                </v-col>
                <v-col cols="12" class="pt-0 pb-2 px-5">
                    <v-btn
                    v-for="(item, i) in lesson_settings"
                    :key="`lessonbtn-${i}`"
                    small block depressed outlined
                    style="text-transform: capitalize;"
                    :style="item.status == 1 ? 'border: 1px solid yellowgreen !important;': 'border: 1px solid lightgray;'"
                    :class="['mb-1', item.status == 1 ? 'light-green lighten-5 success--text': 'grey--text']"
                    @click="changeSetting('lesson', item.id)"
                    >
                        <b>{{ item.text }}</b>
                    </v-btn>
                </v-col>
            </v-row>

            <v-divider
            style="width:90%; margin:0 auto;"
            >
            </v-divider>

            <v-row style="width: 100%; margin: 0 auto;">
                <v-col cols="12" class="pa-2">
                    <b class="warning--text">시험 과정</b>
                </v-col>
                <v-col cols="12" class="pt-0 pb-2 px-5">
                    <v-btn
                    v-for="(item, i) in test_settings"
                    :key="`testbtn-${i}`"
                    small block depressed outlined
                    style="text-transform: capitalize;"
                    :style="item.status == 1 ? 'border: 1px solid yellowgreen !important;': 'border: 1px solid lightgray;'"
                    :class="['mb-1', item.status == 1 ? 'light-green lighten-5 success--text': 'grey--text']"
                    @click="changeSetting('test', item.id)"
                    >
                        <b>{{ item.text }}</b>
                    </v-btn>
                </v-col>
            </v-row>

            <v-divider
            style="width:90%; margin:0 auto;"
            >
            </v-divider>

            <v-row class="bg-white text-center"
            style="width:100%; margin: 0 auto;"
            v-if="class_item_select_status == true"
            >
                <v-col class="pa-0" :align-self="'center'">
                    <v-list class="py-0" dense>
                        <v-list-item-group multiple active-class="">

                            <v-list-item
                            v-for="i in 2"
                            :key="`optionkey-${i}`"
                            >
                                <template v-slot:default="{ active }">
                                    <v-list-item-action
                                    style="margin-right: 5px;"
                                    >
                                        <v-checkbox v-model="active" color="info" :id="`test${i}`">
                                        </v-checkbox>
                                    </v-list-item-action>

                                    <v-list-item-content>
                                        <v-list-item-title>
                                            <span>{{ active }} {{ i == 1 ? '단어학습 시 이미지 표기' : '교제 완료 시 통합 평가출제' }}</span>
                                        </v-list-item-title>
                                    </v-list-item-content>
                                </template>
                            </v-list-item>
                        </v-list-item-group>
                    </v-list>
                </v-col>
            </v-row>
            </section>
        </div>

        <v-row class="bg-white text-center"
        v-if="class_item_select_status == true"
        style="height: calc(7.5% - 4px); width: 100%; margin: 0 auto;"
        >
            <v-col class="pa-0" :align-self="'center'" style="height: 100%;">
                <v-btn
                block depressed tile
                color="success"
                style="height: 100%;"
                @click="openDialog('settingDialog')"
                >
                    <b>수업 스케쥴 설정</b>
                </v-btn>
            </v-col>
        </v-row>

    </v-col>

    <Dialogs>
        <div slot="recommend_confirm">
            <v-card-text class="text-center">
                <h4><b>현재 학습설정을 권장합니다.</b></h4>
                <h4><b>그래도 수정하시겠습니까?</b></h4>
            </v-card-text>

            <!-- Two Button -->
            <v-card-actions class="pa-0" style="border-top: 1px solid lightgray;">
                <v-btn x-large text style="border-radius: 0; border-bottom-left-radius: 4px; border-right: 1px solid lightgray; width: 50%" @click="closeDialog">
                    <b class="success--text">취소</b>
                </v-btn>
                <v-btn x-large class="ml-0" text style="width:50%;" @click="continueEve">
                    <b>수정</b>
                </v-btn>
            </v-card-actions>
        </div>

        <div slot="reject_writing">
            <v-card-text class="text-center">
                <h4><b>Writing 단계는 필수 학습단계입니다.</b></h4>
                <h4><b>해제할 수 없습니다.</b></h4>
            </v-card-text>

            <v-card-actions class="pa-0" style="border-top: 1px solid lightgray;">
                <v-btn x-large text style="border-radius: 0; border-bottom-left-radius: 4px; border-right: 1px solid lightgray; width: 100%;" @click="closeDialog">
                    <b>확인</b>
                </v-btn>
            </v-card-actions>
        </div>

        <div slot="reject_cnt">
            <v-card-text class="text-center">
                <h4><b>최소 2개 이상 모듈이 설정되어야 합니다.</b></h4>
                <h4><b>더 이상 해제할 수 없습니다.</b></h4>
            </v-card-text>

            <v-card-actions class="pa-0" style="border-top: 1px solid lightgray;">
                <v-btn x-large text style="border-radius: 0; border-bottom-left-radius: 4px; border-right: 1px solid lightgray; width: 100%;" @click="closeDialog">
                    <b class="success--text">확인</b>
                </v-btn>
            </v-card-actions>
        </div>

        <div slot="settingDialog">
            <v-card-title class="text-center justify-center">
                <h4 class="success--text"><b>수업 스케쥴</b></h4>
            </v-card-title>

            <v-divider style="width: 50%; margin: 0 auto; border: 1px dashed rgba(0, 0, 0, 0.12)"></v-divider>

            <v-card-text class="text-center">
                <v-row>
                    <v-col class="text-right">
                        <v-btn
                        depressed
                        :style="select_option == 0 ? 'border: 2px solid #1bd05b' : ''"
                        :class="[select_option == 0 ? 'success--text': 'grey--text']"
                        @click="select_option = 0"
                        >
                            <b>단원별 설정</b>
                            <v-icon small right v-if="select_option == 0">mdi-check</v-icon>
                        </v-btn>
                    </v-col>
                    <v-col class="text-left">
                        <v-btn
                        depressed
                        v-if="select_option == 0"
                        @click="select_option = 1"
                        >
                            문항별 설정
                        </v-btn>

                        <v-btn
                        depressed
                        style="border: 2px solid #1bd05b;"
                        class="success--text"
                        v-if="select_option == 1"
                        >
                            <v-icon small left @click="changeSentenceNum('left')">mdi-chevron-left</v-icon>
                            {{ select_sentence_num }}개
                            <v-icon small right @click="changeSentenceNum('right')">mdi-chevron-right</v-icon>
                        </v-btn>
                    </v-col>
                </v-row>

                <v-row>
                    <v-col :align-self="'center'">
                        <h2>
                            <small class="grey--text mr-2" style="vertical-align: middle;">전체 문장</small>
                            <b class="success--text mr-7">292개</b>
                            <small class="grey--text mr-2" style="vertical-align: middle;">학습 일차</small>
                            <b class="success--text">7{{ select_option == 0 ? '단원' : '차시' }}</b>
                        </h2>
                    </v-col>
                </v-row>
            </v-card-text>

            <v-card-actions class="pa-0" style="border-top: 1px solid lightgray;">
                <v-btn x-large text style="border-radius: 0; border-bottom-left-radius: 4px; border-right: 1px solid lightgray; width: 100%;" class="success" @click="submitSchedule">
                    <b class="white--text">적용</b>
                </v-btn>
            </v-card-actions>
        </div>

    </Dialogs>
</v-row>
</template>

<style lang="scss">
@import '@/assets/scss/views/lesson/schedule.scss';
@import "@/assets/scss/_mixins";
$breakpoints: (
    xs: 600px,
    sm: 960px,
    md: 1264px,
    lg: 1904px
);
#user-alignclass {
    .v-expansion-panel {
        & > .v-expansion-panel-header {
            flex-direction: row-reverse !important;

            .v-expansion-panel-header__icon {
                margin-left: unset !important;
                margin-right: auto;
            }
        }
    }
}
#assign-act-footer {
    background-color: white;

    @include respond-below(xs) {
        width: calc(50% -44px) !important;
    }
    @include respond-between(xs,sm) {
        width: calc(50% - 94px) !important;
    }
    @include respond-between(sm,md) {
        width: calc(50% - 114px) !important;
    }
    @include respond-above(md) {
        width: calc(50% - 134px) !important;
    }
}
.after-doticon {
    width: 24px;
    height: 24px;
}
</style>

<script>
export default {
    name: "lesson-schedule",
    components: {

    },
    data() {
        return {
            settings: [],
            class_items: [],
            class_item_select_status: false,
            class_item_lesson_day: '',
            class_students: [],
            select_class_students: [],
            class_students_settings: [],
            select_books_settings: {
                '1': [],
                '2': [],
                '3': []
            },
            select_grade: {
                '1': 'all',
                '2': 'all',
                '3': 'all'
            },

            tab: null,
            tabs: 3,
            grade_items : [
                { text: '전체', value: 'all' },
                { text: '초1', value: 'e1' },
                { text: '초2', value: 'e2' },
                { text: '초3', value: 'e3' },
                { text: '초4', value: 'e4' },
                { text: '초5', value: 'e5' },
                { text: '초6', value: 'e6' },
                { text: '중1', value: 'm1' },
                { text: '중2', value: 'm2' },
                { text: '중3', value: 'm3' },
                { text: '고1', value: 'h1' },
                { text: '고2', value: 'h2' },
                { text: '고3', value: 'h3' },
            ],
            book_items: [],
            select_book_items: [],

            lesson_settings: [
                { id: 1, text: 'Matching', status: 1 },
                { id: 2, text: 'Dictation', status: 1 },
                { id: 3, text: 'Pop Quiz', status: 1 },
                { id: 4, text: 'Writing', status: 1 },
            ],
            test_settings: [
                { id: 1, text: 'Matching', status: 1 },
                { id: 2, text: 'Dictation', status: 1 },
                { id: 5, text: 'Writing', status: 1 },
                { id: 6, text: 'Speaking', status: 1 },
            ],

            first_status: 0,
            ftmp1: '',
            ftmp2: '',

            select_option: 0,
            select_sentence_num: 15,
        }
    },
    created() {
        // class 조회
        this.$axios.get(`${this.$host}/class/` + this.$cid + '/getclassroom')
        .then((res) => {
            if (res.status == 200) {
                for (let val of Object.entries(res.data)) {
                    this.class_items.push({
                        text: val[1].name,
                        value: val[1].id,
                        lesson_day: !!val[1].lesson_day ? JSON.parse(val[1].lesson_day).join(',') : '',
                    });
                }
            }

        }).catch((err) => {
            console.log(err);
        });

    },
    mounted() {
        // 교과서
        this.getBookList(2);
        // 수능모의고사
        this.getBookList(3);
        // 만든 교재
        this.getBookList(4);

        // this.openDialog('settingDialog');
    },
    watch: {

    },
    methods: {
        // selectClassAll(e, val){
        //     e.stopPropagation();
        //     console.log(e);
        //     console.log(val);
        // },
        //

        changeTab() {

            // this.select_books_settings = [];

        },
        getBookList(category){

            let url = `${this.$host}/book/getlist/` + category;
            if(category == 4)
                url += '/' + this.$cid;

            this.$axios.get(url).then((res) => {
                if (res.status == 200) {
                    this.book_items[category] = [];
                    for (let val of Object.entries(res.data)) {
                        this.book_items[category].push({
                            id: val[1].id,
                            bookname: val[1].bookname,
                            category: val[1].category,
                            grade: val[1].grade,
                            publishdate: val[1].publishdate,
                            publisher: val[1].publisher,
                            total_cnt: val[1].total_cnt,
                            unit_cnt: val[1].unit_cnt,
                        });
                    }
                }

            }).catch((err) => {
                console.log(err);
            });
        },
        getStudentInClass(val) {
            this.class_students = [];
            this.class_students_settings = [];
            const class_id = val;

            let np = new Promise((resolve, reject) => {
                this.$axios.get(`${this.$host}/class/` + this.$cid + '/getstudent/' + class_id)
                .then((res) => {
                    if (res.status == 200) {
                        for (let val of Object.entries(res.data)) {
                            this.class_students.push({
                                text: val[1].uname,
                                value: val[1].user_id,
                                id: val[1].user_id,
                                model: []
                            });
                        }
                        resolve('success');
                    }
                }).catch((err) => {  reject(err)  });
            })

            np.then((res) => {
                if(res == 'success'){
                    this.class_item_select_status = true;

                    let check = this.class_items.find(data => {
                        return data.value == val
                    });
                    this.class_item_lesson_day = check.lesson_day;
                }
            }).catch((err) => {
                console.log(err);
            })

        },

        changeStudentCheckbox(val){

            let check = this.select_class_students.findIndex( data => {
                return data == val;
            });

            if(check == -1)
                this.select_class_students.push(val);
            else if(check != -1)
                this.select_class_students.splice(check, 1);

        },
        selectGradeBtnEvent(tabidx, val){
            // schedule_grade_items

            let allbtn = document.getElementsByClassName('schedule_grade_items_' + tabidx);
            for(let item of allbtn){
                item.classList.remove('success', 'white--text');
            }

            let selbtn = document.getElementById("gradeitembtn-" + tabidx + "-" + val);
            selbtn.classList.add('success', 'white--text');

            if(val != 'all'){
                let retval = '';
                let school = val.substr(0, 1);
                let num = val.substr(1, 1);

                if(school == 'e') retval = '초등학교 ';
                else if(school == 'm') retval = '중학교 ';
                else if(school == 'h') retval = '고등학교 ';

                retval += num + '학년';
                this.select_grade[tabidx] = retval;
            }else{
                this.select_grade[tabidx] = val;
            }

        },
        selectBook(val){

            let check = this.select_book_items.findIndex( data => {
                return data == val;
            });

            if(check == -1)
                this.select_book_items.push(val);
            else if(check != -1)
                this.select_book_items.splice(check, 1);

        },
        continueEve(){
            this.first_status = 1;
            this.closeDialog();
            this.changeSetting(this.ftmp1, this.ftmp2);
        },
        changeSetting(type, id){

            if(this.first_status == 0){
                this.ftmp1 = type;
                this.ftmp2 = id;
                this.openDialog('recommend_confirm');
                return false;
            }

            let check = this[type + '_settings'].findIndex(data => {
                return data.id == id
            });

            if(check != -1){
                let item = this[type + '_settings'][check];

                if(item.text == 'Writing'){
                    this.openDialog('reject_writing');
                    return false;
                }

                let cnt = this[type + '_settings'].filter(data => {
                    return data.status == 0;
                });


                if(item.status == 0){
                    this[type + '_settings'][check]['status'] = 1;
                }else{
                    if(cnt.length == 2){
                        this.openDialog('reject_cnt');
                        return false;
                    }else{
                        this[type + '_settings'][check]['status'] = 0;
                    }
                }
            }

        },

        changeSentenceNum(val){
            if(val == 'left'){
                if( this.select_sentence_num - 5 < 0){
                    return false;
                }else{
                    this.select_sentence_num -= 5;
                }
            }
            else{
                if( this.select_sentence_num + 5 > 100){
                    return false;
                }else{
                    this.select_sentence_num += 5;
                }
            }
        },
        submitSchedule(){

            let lesson_tmp = [];
            let test_tmp = [];
            let img_opt = document.getElementById('test1').getAttribute('aria-checked');
            let eval_opt = document.getElementById('test2').getAttribute('aria-checked');

            for(let [idx, item] of Object.entries(this.lesson_settings)){
                if(item.status == 1){
                    lesson_tmp.push(item.id);
                }
            }

            for(let [idx, item] of Object.entries(this.test_settings)){
                if(item.status == 1){
                    test_tmp.push(item.id);
                }
            }

            alert('console 확인');
            console.log('선택된 학생 ids : ', this.select_class_students);
            console.log('선택된 교재 ids : ', this.select_book_items);
            console.log('선택된 학습 과정 ids : ', lesson_tmp);
            console.log('선택된 시험 과정 ids : ', test_tmp);
            console.log('이미지 표기 여부 : ', img_opt);
            console.log('평가출제 여부 : ', eval_opt);
            console.log('option : ', this.select_option);
            console.log('문항별 설정인 경우 : ', (this.select_option == 0 ? 'null' : this.select_sentence_num) );

        },
    },
    computed: {


    },
};
</script>
