                                                            :placeholder="y==0 ? '영문장' : ''"
                                                            outlined flat color="warning"
                                                            >
                                                            </v-text-field>
                                                        </v-col>
                                                        <v-col cols="5" class="py-0 pl-1 pr-2">
                                                            <v-text-field
                                                            v-model="item.kor"
                                                            :id="`file_kor_${x}_${y}`"
                                                            dense solo single-line hide-details
                                                            :placeholder="y==0 ? '해석' : ''"
                                                            outlined flat color="warning"
                                                            >
                                                            </v-text-field>
                                                        </v-col>
