              </v-card-text>

              <v-card-actions class="pa-0" style="border-top: 1px solid lightgray;">
                  <v-btn x-large text style="border-radius: 0; border-bottom-left-radius: 4px; border-right: 1px solid lightgray; width: 100%;" @click.stop="closeDialog">
                      <b>확인</b>
                  </v-btn>
              </v-card-actions>
          </div>

          <div slot="null_input">
              <v-card-text class="text-center">
                  <h4><b>입력되지 않은 셀이 있습니다.</b></h4>
              </v-card-text>

              <v-card-actions class="pa-0" style="border-top: 1px solid lightgray;">
                  <v-btn x-large text style="border-radius: 0; border-bottom-left-radius: 4px; border-right: 1px solid lightgray; width: 100%;" @click.stop="closeDialog">
                      <b>확인</b>
                  </v-btn>
              </v-card-actions>
