                  <v-btn x-large text style="border-radius: 0; border-bottom-left-radius: 4px; border-right: 1px solid lightgray; width: 50%;" @click.stop="makeBook(sel_type)">
                      <b class="success--text">생성</b>
                  </v-btn>
                  <v-btn x-large class="ml-0" text style="width:50%;" @click="closeDialog">
