<style lang="scss">
@import '@/assets/scss/views/lesson/bookmaking.scss';
</style>

<script>
import vue2Dropzone from 'vue2-dropzone'
import 'vue2-dropzone/dist/vue2Dropzone.min.css'
import { mapGetters } from "vuex";
import LogoImg from "@/components/common/LogoImg"
import LoadingDialog from "@/components/common/LoadingDialog"
import {bus} from '@/main'
/* eslint no-use-before-define: 0 */

let unit_name_arr = [];
let unit_row_arr = [];

export default {
    name: "login",
    components: {
        vueDropzone: vue2Dropzone,
        LogoImg, LoadingDialog
    },
    beforeRouteLeave(to, from, next) {
        // 작성 중인 내용 있을 때 이벤트

        let sum = this.direct_bookname + this.format_bookname + this.file_bookname;

        if(sum == ''){
            next();
        }
        else{
            console.log('not null');
            next();
        }
    },
    data () {
        return{
            tab: 'tab-0',
            tabs: ['직접 생성', '서식 생성', '파일 생성'],
            subtitle_text: '직접 생성',
            vt:0,

            direct_bookname: '',
            direct_total_unit: '',
            direct_total_row: '',

            direct_tab: 'direct-tab-0',
            direct_tabs: ['Unit 1', 'add'],
            direct_input_row: [
                [
                    {
                        eng: '',
                        kor: ''
                    }
                ]
            ],

            direct_book : [{}],
            format_book : [],
            file_book : [],

            formatPaste: false,
            tmp_textarea: '',
            format_textarea: '',
            format_textarea_readonly: false,
            format_btn_disabled: false,
            format_total_sentence: '',
            format_bookname: '',
            format_division_num: '',
            format_tab: 'format-tab-0',
            format_tabs: [{
                name: 'Unit 1',
                model: '',
            }],
            format_input_row: [
                [
                    {
                        eng: '',
                        kor: ''
                    }
                ]
            ],


            dropzoneOptions: {
                url: `${this.$host}/uploadbookexcel`,
                thumbnailWidth: 150,
                thumbnailheight: 106,
                maxFilesize: 2,
                chunking: true,
                headers: {
                    'X-Requested-With': 'XMLHttpRequest',
                    'Accept' : 'application/json, text/html'
                    // 'Accept' : 'text/html'
                },
                dictDefaultMessage : '<span class="black--text">Drag and drop Files Here to Upload.</span><br/><span class="grey--text">(엑셀파일의 첫번째 시트만 인식되며, 첫번째 행은 업로드 제외됩니다.)</span><br/><br/><div class="v-btn v-btn--depressed v-btn--tile theme--light v-size--small info subtitle-1" style="text-transform: capitalize;">Choose Files</div>'
            },
            bookmaking_excel_data : [],
            file_bookname: '',
            file_total_sentence: '',
            file_division_num: '',
            file_tab: 'file-tab-0',
            file_tabs: [{
                name: 'Unit 1',
                model: '',
            }],
            file_input_row: [
                [
                    {
                        eng: '',
                        kor: ''
                    }
                ]
            ],
            file_btn_disabled: false,

            loading_dialog_value: false,
            sel_type: '',
        }
    },
    created : function () {

    },
    watch : {
        tab (val) {
            this.subtitle_text = this.tabs[val.substr( val.length - 1)];
        },
        direct_eng (val) {
            // console.log(val);
        },
        format_bookname (val){
            if( val != "" ){
                this.format_division_num = '0개';
            }else if(val == ''){
                this.format_division_num = '';
            }
        },
        file_bookname(val) {
            if( val != "" ){
                this.file_division_num = '0개';
            }else if(val == ''){
                this.file_division_num = '';
            }
        },
    },
    computed : {

    },
    mounted () {

    },
    methods: {
        pad(n, width) {
            n = n + '';
            return n.length >= width ? n : new Array(width - n.length + 1).join('0') + n;
        },
        changeDirectTab(val){
            if(val == 'add'){
                let dtlength = this.direct_tabs.length;
                this.direct_tabs[dtlength] = this.direct_tabs[dtlength-1];
                this.direct_tabs[dtlength-1] = this.direct_tabs[dtlength-2].split(" ")[0] + " " + (parseInt(this.direct_tabs[dtlength-2].split(" ")[1]) + 1);

                this.direct_input_row.push([{
                    eng: '',
                    kor: ''
                }]);
                // this.direct_total_unit += 1;
                // this.direct_total_row += 1;
            }
        },

        // 로우 클릭 버튼 클릭 시
        addTextRow () {
            let unit_num = parseInt(this.direct_tab.substr( this.direct_tab.length - 1 ));
            this.direct_input_row[unit_num].push({
                eng: '',
                kor: ''
            });
            // this.direct_total_row += 1;
        },

        // null check
        null_check (type) {
            let bool, tmp_num = '';
            return new Promise ((resolve, reject) => {

                if( type == 'format' || type == 'file' ){
                    tmp_num = this[type + '_tabs'].length;
                }

                this[type + '_tabs'].some((xval, x) => {
                    if(xval == 'add'){
                        resolve(false);
                        return true; // break;
                    }

                    this[type + '_input_row'][x].some((yval, y) => {
                        if( yval.eng == "" || yval.kor == "" ){
                            this.openDialog('null_input');
                            bool = true;
                            reject(true);
                            return true;
                        }
                    });

                    if(bool == true){
                        reject(true);
                        return true;
                    }

                    if(x == tmp_num - 1){
                        resolve(false);
                    }

                });
            });
        },

        process_data (type) {
            unit_name_arr = [];
            unit_row_arr = [];
            return new Promise((resolve, reject) => {
                if( type == 'direct' ){
                    for (let i = 0; i < this[type + '_tabs'].length - 1; i++) {
                        let tmp_unit_name = document.getElementById(type + '-unit-name-0-' + i).value;
                        unit_name_arr[i] = (tmp_unit_name == '') ? this[type + '_tabs'][i] : tmp_unit_name;

                        unit_row_arr[i] = [];
                        for (let y = 0; y < this[type + '_input_row'][i].length; y++) {
                            unit_row_arr[i][y] = this[type + '_input_row'][i][y];
                        }
                    }
                    resolve(true);
                }

                else if( type == 'format' || type == 'file' ){
                    for (let i = 0; i < this[type + '_tabs'].length; i++) {

                        let checkunit = document.getElementById(type + '-unit-name-1-' + i);
                        let tmp_unit_name = (checkunit == null || checkunit.value == '') ? 'Unit ' + (i+1) : checkunit.value;

                        unit_name_arr[i] = (tmp_unit_name == '') ? this[type + '_tabs'][i] : tmp_unit_name;

                        unit_row_arr[i] = [];
                        for (let y = 0; y < this[type + '_input_row'][i].length; y++) {
                            unit_row_arr[i][y] = this[type + '_input_row'][i][y];
                        }
                    }
                    resolve(true);
                }
            });

        },

        addbook (type) {
            return new Promise((resolve, reject) => {
                this.$axios.post(`${this.$host}/book/` + this.$cid + '/addbook', {
                    bookname: this[type + '_bookname'],
                    category: 4,
                    total_cnt: 0,
                    unit_cnt: 0,
                }).then((res) => {
                    if(res.status == 200)
                        resolve(res.data);
                }).catch((err) => {  reject(err)  });
            });
        },

        addunit (bid) {
            return new Promise((resolve, reject) => {
                this.$axios.post(`${this.$host}/book/addunit`, {
                    book_id: bid,
                    name: unit_name_arr
                }).then((res) => {
                    if(res.status == 200){
                        resolve(res.data);
                    }
                }).catch((err) => {  reject(err)  });
            });
        },

        addsentences (uids) {
            return new Promise((resolve, reject) => {
                this.$axios.post(`${this.$host}/book/addsentences`, {
                    unit_id: uids,
                    unit_row: unit_row_arr
                }).then((res) => {
                    if(res.status == 200){
                        resolve(res.data);
                    }
                }).catch((err) => {  reject(err)  });
            });
        },

        makeBookConfirm(type){
            if( this[type + '_bookname'] == ''){
                this.openDialog('null_book_name');
                return false;
            }

            this.null_check(type).then((res) => {
                console.log( res );
                if(res == false){
                    this.sel_type = type;
                    this.openDialog("make_book_confirm");
                }
            }).catch((err) => { console.log(err); })

        },

        // 교재 생성 클릭 시
        makeBook(type){

            this.closeDialog();

            let tmpres = '';
            // loading on
            this.loading_dialog_value = true;

            // promise chain
            this.process_data(type)
            .then((res) => {
                return this.addbook(type)
            })
            .then((res) => {
                if( parseInt(res) > 0 ){
                    return this.addunit(res);
                }
            })
            .then((res) => {
                return this.addsentences(res);
            })
            .then((res) => {
                this.loading_dialog_value = false;
                this.$router.push({name:'lesson-booklist', params:{
                    dialog:'success',
                }});
            })
            .catch((err) => {
                this.loading_dialog_value = false;
            });

        },

        // 텍스트 필드 포커스 이벤트
        textFieldFocusEvent (x, y) {
            if(y != 0){
                document.getElementById("direct_span_" + x + "_" + y).classList.remove('d-block');
                document.getElementById("direct_span_" + x + "_" + y).classList.add('d-none');

                document.getElementById("direct_close_" + x + "_" + y).classList.remove('d-none');
                document.getElementById("direct_close_" + x + "_" + y).classList.add('d-inline-flex');
            }
        },

        // 텍스트 필드 블러 이벤트
        textFieldBlurEvent (x, y) {
            if(y != 0){
                setTimeout(function(){
                    if( document.getElementById("direct_span_" + x + "_" + y) ){
                        document.getElementById("direct_span_" + x + "_" + y).classList.remove('d-none');
                        document.getElementById("direct_span_" + x + "_" + y).classList.add('d-block');

                        document.getElementById("direct_close_" + x + "_" + y).classList.remove('d-inline-flex');
                        document.getElementById("direct_close_" + x + "_" + y).classList.add('d-none');
                    }
                }, 100);
            }
        },
        directCloseClick (x, y) {
            this.direct_input_row[x].splice(y, 1);
        },
        directKeyup (x, y, e){
            // console.log(x, y, e);

            // if ( e.keyCode != 8 && e.keyCode != 9 )

            // 학습일차, 전체문장 설정
            // if(x == 0 && y == 0){
            //     if( this.direct_total_row == "" ){
            //         if( this.direct_input_row[x][y].eng != '' && this.direct_input_row[x][y].kor != '' ){
            //             this.direct_total_unit = '1차시';
            //             this.direct_total_row = '1개';
            //         }
            //     }else{
            //         if( this.direct_input_row[x][y].eng == '' && this.direct_input_row[x][y].kor == '' ){
            //             let dtr = parseInt(this.direct_total_row.replace("개", ""));
            //             if( dtr != 1 ){
            //                 this.direct_total_row = (dtr - 1) + '개';
            //             }else{
            //                 this.direct_total_row = '';
            //             }
            //         }
            //     }
            // }
            // else{
            //     console.log( this.direct_total_row.substr(0, 1) );
            //     if( this.direct_input_row[x][y].eng != '' && this.direct_input_row[x][y].kor != '' ){
            //         // this.direct_total_unit = '1차시';
            //         this.direct_total_row = (parseInt(this.direct_total_row.replace("개", "")) + 1) + '개';
            //     }
            // }
        },
        formatPasteEvent(e){
            this.formatPaste = true;
        },
        formatTextareaEvent(val){
            if(this.formatPaste){
                let new_arr = [];
                let origin_arr = '';

                // 기존 붙혀넣기 값 있을 때
                if( val.match(/\t/gi) ){
                    if( this.tmp_textarea != '' ){
                        let diff_val = val.replace(this.tmp_textarea, ''); // 기존 값과 다른 부분
                        diff_val = diff_val.replace(/(^\s*)|(\s*$)/gi, ""); // 앞뒤 공백 제거

                        diff_val = diff_val.split('\n');
                        origin_arr = diff_val;

                        for(let i=0; i < origin_arr.length; i++){
                            if( i % 2 == 0){
                                new_arr.push( origin_arr[i] );
                                new_arr.push( '\t' );
                            }else{
                                new_arr.push( origin_arr[i] );
                                new_arr.push( '\n' );
                            }
                        }
                    }
                    val = this.tmp_textarea + new_arr.join().replace(/\,/g, '');
                    this.format_textarea = val;
                    this.tmp_textarea = val;
                }

                // 기존 붙혀넣기 값 없을때
                else{
                    val = val.split('\n');
                    val.splice( 0, 1 );
                    val.splice( val.length - 1, 1 );

                    origin_arr = val;

                    for(let i=0; i < origin_arr.length; i++){
                        if( i % 2 == 0){
                            new_arr.push( origin_arr[i] );
                            new_arr.push( '\t' );
                        }else{
                            new_arr.push( origin_arr[i] );
                            new_arr.push( '\n' );
                        }
                    }

                    this.format_textarea = new_arr.join().replace(/\,/g, '');
                    this.tmp_textarea = new_arr.join().replace(/\,/g, '');
                }
                document.getElementById('format_apply_btn').classList.remove('d-none');
                document.getElementById('format_apply_btn').classList.add('d-block');
                this.formatPaste = false;
            }

            // 직접 입력
            else{
                //
            }

        },
        formatApply(){
            // 단순 엔터 제거
            let tmp = this.format_textarea.split('\n');
            while( tmp.lastIndexOf('') != -1 ){
                tmp.splice( tmp.lastIndexOf(''), 1 );
            }

            let count = (this.format_textarea.match(/\n/g) || []).length;
            this.format_total_sentence = count + '개';

            document.getElementById('format_title_section').classList.remove('d-none');
            document.getElementById('format_title_section').classList.add('d-block');
            this.format_textarea_readonly = true;
        },
        Division(type, val){

            let dnum = parseInt(this[type + '_division_num'].replace("개", ""));
            let tsnum = parseInt(this[type + '_total_sentence'].replace("개", ""));

            if(val == 'prev'){
                if(dnum - 5 >= 0 ){
                    this[type + '_division_num'] = (dnum - 5) + '개';
                }
            }
            else if(val == 'next'){
                if(dnum + 5 <= tsnum){
                    this[type + '_division_num'] = (dnum + 5) + '개';
                }
            }

        },
        DivisionApply(type){

            if( this[type + '_bookname'] == '' ){
                this.openDialog('null_book_name');
            }else{
                let dnum = parseInt(this[type + '_division_num'].replace("개", ""));
                let tsnum = parseInt(this[type + '_total_sentence'].replace("개", ""));

                let row = '';
                if( type == 'format' ){
                    row = this.format_textarea.split('\n');
                    row.splice( row.lastIndexOf(''), 1 );
                }
                else if( type == 'file' ){
                    row = this.bookmaking_excel_data;
                }

                // 그외
                if( isFinite(tsnum / dnum) == true ){
                    this[type + '_tabs'] = [{ name : 'Unit 1', model: '' }];

                    let total_tabnum = Math.ceil(tsnum / dnum);
                    for(let i = 0; i < total_tabnum; i++){
                        if(i != 0){
                            this[type + '_tabs'].push({ name : 'Unit ' + (i + 1), model : '' });
                        }
                        this[type + '_input_row'][i] = [];
                        for(let y = (i * dnum); y < (i * dnum + dnum); y++){
                            if( y < tsnum ){
                                if(type == 'format'){
                                    this[type + '_input_row'][i].push({
                                        eng: row[y].split('\t')[0],
                                        kor: row[y].split('\t')[1]
                                    });
                                }
                                else if(type == 'file'){
                                    this[type + '_input_row'][i].push({
                                        eng: row[y][0],
                                        kor: row[y][1]
                                    });
                                }

                            }
                        }
                    }
                }
                // 0으로 나누었을때
                else{
                    this[type + '_tabs'] = [{ name : 'Unit 1', model: '' }];
                    this[type + '_input_row'][0] = [];
                    for(let i = 0; i < row.length; i++){
                        if(type == 'format'){
                            this[type + '_input_row'][0].push({
                                eng: row[i].split('\t')[0],
                                kor: row[i].split('\t')[1]
                            });
                        }
                        else if(type == 'file'){
                            this[type + '_input_row'][0].push({
                                eng: row[i][0],
                                kor: row[i][1]
                            });
                        }

                    }

                }

                this[type + '_btn_disabled'] = true;
                document.getElementById(type + '_tab_section').classList.remove('d-none');
                document.getElementById(type + '_tab_section').classList.add('d-block');
            }

        },

        downloadSample() {
            this.$axios({
              url: `${this.$host}/bookmakingsample`,
              method: 'GET',
              responseType: 'blob', // important
              headers: { 'Accept': 'application/vnd.ms-excel' },
            }).then((response) => {
              const url = window.URL.createObjectURL(new Blob([response.data]));
              const link = document.createElement('a');
              link.href = url;
              link.setAttribute('download', 'bookmaking_example.xlsx');
              document.body.appendChild(link);
              link.click();
          }).catch((err) => {
              console.log(err);
          });
        },
        afterComplete(file, response) {
            // console.log(file);
            // console.log(response);
            if(file.status == 'success'){
                document.getElementById("file_title_section").classList.remove('d-none');
                document.getElementById("file_title_section").classList.add('d-block');
            }
        },
        uploadSuccess (file, response) {
            // console.log("uploadSuccess");
            // console.log( response.length );
            this.file_total_sentence = response.length + '개';
            for(var i=0; i < response.length; i++) {
                this.bookmaking_excel_data.push(response[i]);
            }

            //this.$refs.excelform.validate();
        },
        showError(a,b,c) {
            // window.console.log(a, b, c);
        },
        setFieldError(err, id) {
            let field = document.getElementById(id).parentNode.parentNode.parentNode.parentNode;
            // if (err) {
            //     field.classList.add( 'error--text' );
            // } else if (field.classList.contains( 'error--text' )) {
            //     field.classList.remove( 'error--text' );
            // }

        },
    },


};
</script>
