import Vue from 'vue'
import App from './App.vue'
import router from "./router"
import vuetify from './plugins/vuetify'
import {store} from './store'
import axios from 'axios'
import {Vue2Storage} from 'vue2-storage'
export const bus = new Vue();

import LayoutDefault from "./layouts/LayoutDefault";
import LayoutLogin from "./layouts/LayoutLogin";
import LayoutAdmin from "./layouts/LayoutAdmin";
import LayoutSetup from "./layouts/LayoutSetup";

import Dialogs from "./components/common/Dialogs";

Vue.component('layout-default', LayoutDefault);
Vue.component('layout-login', LayoutLogin);
Vue.component('layout-setup', LayoutSetup);
Vue.component('layout-admin', LayoutAdmin);

Vue.component('Dialogs', Dialogs);

// session storage config
Vue.use(Vue2Storage, {
    prefix: 'ng_',
    driver: 'session',
    ttl: 60 * 60 * 24 * 1000
});

// global variable
Vue.prototype.$axios = axios;
Vue.prototype.$host = 'http://106.10.52.151';

let globalData = new Vue({
    data : {
        $cid : ''
    }
})

Vue.mixin({
    methods : {
        closeDialog : function (eve) {
            bus.$emit('dialog', false);
            if(eve == true){
                bus.$emit('close_event', {status: true});
            }
        },
        openDialog : function (dname) {
            bus.$emit('dialog', {status: true, name: dname});
        }
    },
    computed : {
        $cid : {
            get : function () { return globalData.$data.$cid },
            set : function (val) { globalData.$data.$cid = val; }
        }
    }
})

Vue.config.productionTip = false;

new Vue({
  vuetify,
  router,
  render: h => h(App),
  store
}).$mount('#app')
